  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/**UI**/
.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}

.footer {
  background-color: #585858;
  padding: 4rem 0;
  color: #FFFFFF;
}
.footer .contenedor-footer {
  max-width: 120rem;
  margin: 0 auto;
  padding: 0 2rem;
}
.footer .footer-top {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  text-align: center;
}
@media (min-width: 768px) {
  .footer .footer-top {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-align: left;
  }
}
.footer {
  /* Redes sociales */
}
.footer .redes-sociales {
  display: flex;
  gap: 15px;
}
.footer .redes-sociales a {
  font-size: 24px;
  text-decoration: none;
  transition: transform 0.3s ease;
}
.footer .redes-sociales a:hover {
  transform: scale(1.2);
}
.footer .redes-sociales a p {
  background-color: #FFFFFF;
}
.footer {
  /* Colores reales */
}
.footer .facebook {
  color: #1877F2;
}
.footer .instagram {
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.footer .whatsapp {
  color: #25D366;
}
.footer {
  /* Navegación */
}
.footer .navegacion {
  display: flex;
  gap: 2.5rem;
  justify-content: center;
}
.footer .navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.6rem;
}
.footer .navegacion a:hover {
  color: #f7a027;
}
.footer {
  /* Copyright */
}
.footer .copyright {
  margin-top: 4rem;
  text-align: center;
  font-size: 1.4rem;
  opacity: 0.8;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra img {
    max-width: 100%;
    width: 28rem;
    height: auto;
  }
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}