.barra {
    display: flex;
    flex-direction: column;
    align-items: center;
    

    @include tablet {
        img {
            max-width: 100%;
            width: 28rem;
            height: auto;
        }

        flex-direction: row;
        justify-content: space-between;
        
    }
}

.navegacion {

    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity 0.5s linear,height 0.5s linear;
        @include tablet {
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;


    }

    a {
        color: $blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;

            &:last-of-type {
                margin-right: 0;
            }
        }
    }
}

// Menu de hamburguesa
.mobile-menu {
    @include tablet {
        display: none;
    }

    img {
        width: 5rem;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}